body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.black_nav{
  display: flex;
  background-color: rgb(199, 156, 199);
  width: 100%;
  height: 60px;
  padding-left: 40px;
  color: black;
  margin-bottom: 20px;
  font-family: Arial, Helvetica, sans-serif;
  border-bottom: 0.5px solid rgb(187, 145, 187);
}

.h44 {
  margin-top: 21px;
  font-size: 18px;
}

div{
  cursor: pointer;
  box-sizing : border-box;

}

.list {
  padding-left: 40px;
  text-align: left;
  border-bottom: 0.7px solid black;
  height: 42px;
}

.modal {
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  padding: 18px;
  text-align: left;
  width: 470px;
  background-color: rgb(223, 200, 223);
  border-radius: 20px;
  border: 1px solid rgb(187, 145, 187);

}

.button {
  padding: 8px;
  border-radius: 10px;
  background-color:rgb(249, 238, 238);
  color: black;
  border: 1px solid rgb(186, 160, 160);
}

.input {
  width: 210px;
  height: 18px;
  border: 0px solid rgb(155, 192, 69);
  background-color:rgb(243, 229, 229);
  border-radius: 10px;
  padding: 8px;
}

.contentDiv {
  display: flex;

}

.textSpace {
  margin-top: 11px;
  width: 310px;
  height: 40px;
}

.titleandx {
  display: flex;
}
.title {
  font-weight: bold;
  margin: 0;
  margin-top: -10px;
  margin-bottom: 17px;
  font-size: 18px;

}
.button2 {
  margin-left: 400px;
  padding: 3px;
  height: 30px;
  width: 40px;
  border-radius: 20px;
  border: 1px solid rgb(187, 145, 187);
  background-color: white;
  font-size: 17px;
}